<script lang="coffee">

  
  import { onMount, createEventDispatcher } from "svelte"
  export value = undefined

  sliderValue = undefined

  dispatch = new createEventDispatcher



  translatedValue = (val)->
    switch val
      when 0
        "0d"
      when 5
        "1d" 
      when 20
        "7d"
      when 30
        "14d"
      when 40
        "1m"
      when 55
        "3m"
      when 70
        "6m"
      when 90
        "12m"
      else
        "all"



  label = (val)->
    if sliderValue < 3
      "Today"
    else if sliderValue >= 3 and sliderValue < 10
      "1 day"
    else if sliderValue >= 10 and sliderValue < 25
      "7 days"
    else if sliderValue >= 25 and sliderValue < 35
      "14 days"
    else if sliderValue >= 35 and sliderValue < 45
      "1 month"
    else if sliderValue >= 45 and sliderValue < 60
      "3 months"
    else if sliderValue >= 60 and sliderValue < 75
      "6 months"
    else if sliderValue >= 75 and sliderValue < 95
      "12 months"
    else
      "All Time"

  # 12.5  / 3
  snap = ()->
    if sliderValue < 3
      sliderValue = 0
    else if sliderValue >= 3 and sliderValue < 10
      sliderValue = 5
    else if sliderValue >= 10 and sliderValue < 25
      sliderValue = 20
    else if sliderValue >= 25 and sliderValue < 35
      sliderValue = 30
    else if sliderValue >= 35 and sliderValue < 45
      sliderValue = 40
    else if sliderValue >= 45 and sliderValue < 60
      sliderValue = 55
    else if sliderValue >= 60 and sliderValue < 75
      sliderValue = 70
    else if sliderValue >= 75 and sliderValue < 95
      sliderValue = 90
    else
      sliderValue = 100
    dispatch "update", translatedValue(sliderValue)





  convertValue = (val)->
    switch val
      when "0d"
         sliderValue = 0
      when "1d"
         sliderValue = 5
      when "7d"
         sliderValue = 20
      when "14d"
         sliderValue = 30
      when "1m"
         sliderValue = 40
      when "3m"
         sliderValue = 55
      when "6m"
         sliderValue = 70
      when "12m"
         sliderValue = 90
      else
        sliderValue = 100
    snap()
    console.log "converted #{val}"

  
  onMount ()->
    convertValue(value)

  `$: horizonLabel = label(sliderValue)`

</script>

<div horizon>

  <div>Time Distance: {horizonLabel}</div>
  <div container>
    <div slider>
    <input bind:value={sliderValue} type="range" id="horizon" name="horizon" min="0" max="100" on:change={snap}>
    </div>
    <div ticks>
      <table>
        <tr>
          <td width="5%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="15%">&nbsp;</td>
          <td width="20%">&nbsp;</td>
          <td width="10%">&nbsp;</td>
        </tr>  

      </table>
      <table style="width: 100%">
        <tr>
          <td width="4%" on:click={convertValue("0d")}>
            0
          </td>
          <td width="15%" on:click={convertValue("1d")}>
            1d
          </td>
          <td width="10%" on:click={convertValue("7d")}>
            7d
          </td>
          <td width="10%" on:click={convertValue("14d")}>
            14d
          </td>
          <td width="15%" on:click={convertValue("1m")}>
            1m
          </td>
          <td width="15%" on:click={convertValue("3m")}>
            3m
          </td>
          <td width="20%" on:click={convertValue("6m")}>
            6m
          </td>
          <td width="10%" on:click={convertValue("12m")}>
            12m      
          </td>
          <td on:click={convertValue("all")}>
            all
          </td>
        </tr>    
      </table>
    </div>
  </div>

</div>
<style lang="sass">

  [horizon]
    margin: 36px 24px
  div[container]
    width: 100%
    background: transparent
    height: 72px
    
    [slider]
      top: 0px
      background: transparent
      z-index: 5
    [ticks]
      position: absolute
      top: 16px
      height: 12px
      width: 100%
      // background: red
      padding: 0px
      table:first-child
        width: 100%
        margin: 0px
        tr
          &:first-child
            td 
              
              border-left: 1px solid #c8c8c8
              border-right: 0px
              &:last-child
                border-right: 1px solid #c8c8c8
          td 
            &:first-child
              text-align: left
            text-align: right


  input[type="range"]
    padding: 6px 0px
    border: 0
      
  div
    text-align: center


  input[type=range]
    width: 100%
    margin: 10px 0
    background-color: transparent
    -webkit-appearance: none

    &:focus
      outline: none

    &::-webkit-slider-runnable-track
      background: #3071a9
      border: 0.4px solid #c8c8c8
      width: 100%
      height: 0px
      cursor: pointer

    &::-webkit-slider-thumb
      margin-top: -10.4px
      width: 20px
      height: 20px
      background: #bcbcbc
      border: 0
      border-radius: 2px
      cursor: pointer
      -webkit-appearance: none

    &:focus::-webkit-slider-runnable-track
      background: #367ebd

    &::-moz-range-track
      background: #3071a9
      border: 0.4px solid #c8c8c8
      width: 100%
      height: 0px
      cursor: pointer

    &::-moz-range-thumb
      width: 20px
      height: 20px
      background: #bcbcbc
      border: 0
      border-radius: 2px
      cursor: pointer

    &::-ms-track
      background: transparent
      border-color: transparent
      border-width: 10px 0
      color: transparent
      width: 100%
      height: 0px
      cursor: pointer

    &::-ms-fill-lower
      background: #2a6495
      border: 0.4px solid #c8c8c8

    &::-ms-fill-upper
      background: #3071a9
      border: 0.4px solid #c8c8c8

    &::-ms-thumb
      width: 20px
      height: 20px
      background: #bcbcbc
      border: 0
      border-radius: 2px
      cursor: pointer
      margin-top: 0px

    // Needed to keep the Edge thumb centred

    &:focus
      &::-ms-fill-lower
        background: #3071a9

      &::-ms-fill-upper
        background: #367ebd
 

</style>